export default {
    name: "pay-info",
    data: function () {
        return {}
    },
    mounted() {},
    methods: {
        pay() {
            if(!this.$route.query.orderId){
              // 
                return  this.$vux.toast.text('id')
            }
            this.$http
                .post(
                    "https://wx.sharkmeida.cn/api/wxpay/prepay?user_id=" + this.$route.query.userId + "&total_fee=" + this.$route.query.prize + "&orderId=" +  this.$route.query.orderId, {}
                )
                .then(({
                    data: res
                }) => {
                    if(res.code == '0000'){
                        WeixinJSBridge.invoke('getBrandWCPayRequest', {
                            appId: "wx2517d7a8920ab213",
                            timeStamp: res.result.timestamp,
                            //  32 
                            nonceStr: res.result.nonceStr,
                            // prepay_idprepay_id=\*\*\*
                            package: res.result.package,
                            // 'SHA1''MD5'
                            signType: "MD5",
                            // 
                            paySign: res.result.paySign,
                        },
                        (res) => {
                            //
                            if (res.err_msg == 'get_brand_wcpay_request:ok') {
                                this.$vux.toast.text("", 'top')
                            } else {
                                this.$vux.toast.text("", 'top')
                            }
                        });
                    }else{
                        this.$vux.alert.show({
                            title: "",
                            content: res.msg
                        });
                    }
                    
                })

        }
    },
}



